.works-color {
  background-color: #efeeee;
}
.works-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}
.works-container .mimicry-container {
  display: flex;
  height: 230px;
  width: 230px;
  margin: 20px;
  background-color: #efeeee;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  align-content: center;
}
.works-container .mimicry-container .mimicry-box {
  width: 200px;
  height: 200px;
  box-shadow: 18px 18px 30px rgba(0, 0, 0, 0.1), -18px -18px 30px #ffffff;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #efeeee;
  transition: box-shadow 0.2s ease-in-out;
}
.works-container .mimicry-container .mimicry-box img {
  width: 150px;
  transition: box-shadow 0.2s ease-in-out;
}
.works-container .mimicry-container .mimicry-box:hover {
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.1), 0px 0px 0px #ffffff, inset 18px 18px 30px rgba(0, 0, 0, 0.1), inset -18px -18px 30px #ffffff;
  transition: box-shadow 0.2s ease-out;
}
